/* global chrome */

import { API_BASE_URL } from './config';

function getStoredToken() {
  return new Promise((resolve) => {
    chrome.storage.local.get(['accessToken', 'csrfToken'], (res) => {
      resolve({
        accessToken: res.accessToken || '',
        csrfToken: res.csrfToken || '',
      });
    });
  });
}

/**
 * CSRF 토큰을 서버에서 가져오는 함수
 */
async function getCsrfToken() {
  try {
    const response = await fetch(`${API_BASE_URL}/api/csrf-token`, {
      credentials: 'include', // 쿠키 포함
    });
    if (!response.ok) {
      throw new Error(`Failed to fetch CSRF token: ${response.status}`);
    }
    const data = await response.json();
    return data.csrfToken;
  } catch (error) {
    console.error('[sendReservations] Failed to get CSRF token:', error);
    throw error;
  }
}

export async function sendReservations(hotelId, siteName, reservations, providedAccessToken = null, providedCsrfToken = null) {
  const { accessToken: storedAccessToken, csrfToken: storedCsrfToken } = providedAccessToken && providedCsrfToken
    ? { accessToken: providedAccessToken, csrfToken: providedCsrfToken }
    : await getStoredToken();

  console.log('[sendReservations] using token:', storedAccessToken);

  if (siteName === 'Agoda') {
    reservations = reservations.map((r) => {
      if (r.checkIn instanceof Date) {
        const inTime = r.checkIn.getTime() + 9 * 60 * 60 * 1000; // UTC+9로 조정
        r.checkIn = new Date(inTime);
      }
      if (r.checkOut instanceof Date) {
        const outTime = r.checkOut.getTime() + 9 * 60 * 60 * 1000; // UTC+9로 조정
        r.checkOut = new Date(outTime);
      }
      return r;
    });
  }

  try {
    let csrfToken = storedCsrfToken;
    if (!csrfToken) {
      csrfToken = await getCsrfToken(); // CSRF 토큰이 없으면 서버에서 새로 가져오기
      chrome.storage.local.set({ csrfToken }, () => {
        console.log('[sendReservations] Updated CSRF token in storage');
      });
    }

    const response = await fetch(`${API_BASE_URL}/api/reservations`, { // 경로 수정: /reservations -> /api/reservations
      method: 'POST',
      headers: {
        Authorization: `Bearer ${storedAccessToken}`,
        'Content-Type': 'application/json',
        'X-CSRF-Token': csrfToken, // CSRF 토큰 추가
      },
      credentials: 'include', // 쿠키 포함
      body: JSON.stringify({
        siteName,
        reservations,
        hotelId,
      }),
    });

    if (!response.ok) {
      throw new Error(`Server responded with status ${response.status}`);
    }

    console.log(`[sendReservations] Sent reservations to ${API_BASE_URL}, hotelId=${hotelId}`);
    return true;
  } catch (error) {
    console.error('[sendReservations] Failed:', error);
    return false;
  }
}